<template>
    <a-layout id="app" class="layout">
        <a-layout-header>
            <h3>eStudy&nbsp&nbsp</h3>
            <student-headers class="student_header" v-if="is_login&&!isTeacher"></student-headers>
            <teacher-headers class="teacher_header" v-else-if="is_login&&isTeacher"></teacher-headers>
        </a-layout-header>
        <a-layout-content>
            <transition enterActiveClass="zoomIn" leaveActiveClass="zoomOut" mode="out-in">
                <login class="animated" v-if="!is_login" @toNext="is_login=true"></login>
            </transition>
            <transition enterActiveClass="zoomIn" leaveActiveClass="zoomOut" mode="out-in">
                <teacher-context class="animated" v-if="this.info.is_admin==1">

                </teacher-context>
                <student-context class="animated" v-else-if="this.info.is_admin==0">

                </student-context>
            </transition>
        </a-layout-content>
    </a-layout>
</template>
<script>
    import {api} from "./utils/api"
    import {mapState,mapActions} from 'vuex'
    import login from "./components/login.vue"
    import studentContext from "./components/studentInfo.vue"
    import teacherContext from "./components/teacherInfo.vue"
    import studentHeaders from "./components/studentHeaderinfo.vue"
    import teacherHeaders from  "./components/teacherHeaderinfo.vue"
     export default {
        name: 'app',
        computed: {
            ...mapState("user", ["is_login","info"]),
            isTeacher(){
                return this.info.is_admin>0
            }
        },
        components: {
            login,
            studentContext,
            studentHeaders,
            teacherContext,
            teacherHeaders
        },
        created(){
            if(this.is_login){
                this.getMessage();
            }
        },
        watch:{
            //
            is_login(val){
                if(val){
                   this.getMessage();
                }
            }
        },
        methods:{
            getMessage(){
                if(this.isTeacher){
                    api.get("/teacher/detail").then(response=>{             //
                        this.$store.dispatch("teacherInfo/Dreload",response);
                        console.log(this.assignments,this.resources,this.orgs)
                    })
                }else {
                    api.get("/student/detail").then(response=>{
                        this.$store.dispatch("studentsinfo/Dreload",response);//
                    }).catch(errors=>{
                        console.log(errors)
                    })
                }
            }
        }
    }
</script>

<style lang="scss">
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        min-height: 100vh;
        min-width: 1200px;
        .ant-layout-header {
            display: flex;
            justify-content: space-between;
            h3 {
                color: white;
            }
            .student_header{
                line-height: normal;
                right: 50px;
                top:0;
                color: white;
            }
        }

    }
</style>
