import {api} from "../../utils/api"
const state ={
    is_login:false,
    info:{

    },
};
const getters ={

};
const mutations ={
    user_login(state,data){
        state.is_login=true;
        state.info=data;
        api.defaults.headers.common["Token"]=state.info.token;
    },
    isLogin(state,token){
        state.info.token=token
    },
    logout(state){
        state.info={};
        state.is_login=false;
        api.defaults.headers.common["Token"]='';
    }
};
const actions ={
    user_login(context,form) {
        api.post("/auth/login",form).then(result=>{
            //result
            context.commit("user_login",result)
        })
    },
    checkLogin(context){
        api.defaults.headers.common["Token"]=context.state.info.token;
        api.post("/auth/refreshToken",{},{_silent:true}).then((token)=>{
            context.commit("isLogin",token)
        }).catch(error=>{
            //token
            context.dispatch("Aclogout");
        })
    },
    Aclogout(context){
        context.commit("logout")
    }
};
export default {
    namespaced:true,
    state,getters,mutations,actions
}