<template>
    <a-form id="form" @submit.prevent="submit">
        <a-form-item>
            <a-input  type="text" placeholder="Username" v-validate="'required'" name="name"  data-vv-as="" v-model="form.name" :class="{errBorder:errors.has('name')}">
                <a-icon
                        slot="prefix"
                        type="user"
                        style="color: rgba(0,0,0,.25)"
                />
            </a-input>
            <span class="err ">{{ errors.first('name') }}</span>
        </a-form-item>
        <a-form-item>
            <a-input :class="{errBorder:errors.has('password')}" placeholder="Password" type="password" v-validate="'required'" name="password" data-vv-as="" v-model="form.password">
                <a-icon
                        slot="prefix"
                        type="lock"
                        style="color: rgba(0,0,0,.25)"
                />
            </a-input>
            <span class="err">{{ errors.first('password')}}</span>
        </a-form-item>
        <a-form-item>
            <a-button
                    type="primary"
                    id="confirm"
                    html-type="submit"
                    :class="{disabled:errors.has('password')||errors.has('name')}"
            >
                
            </a-button>
        </a-form-item>
    </a-form>
</template>
<script>
  import {api} from "../utils/api"
  import {mapState} from "vuex"
  export default{
      data(){
         return{
             form:{
                 name:"",
                 password:""
             }
         }
      },
      computed:{
          ...mapState("user",["is_disable"])
      },
      methods:{
          submit(){
                  this.$validator.validateAll().then(val=>{
                    if(val){
                        this.$store.dispatch('user/user_login',this.form);
                    }
              })
          },
      }
  }
</script>
<style lang="scss">
    #form{
        .err{
            margin-top: 3px;
            color: red;
            font-size: 20px;
        }
        .errBorder{
            border: 1px red solid;
        }
        margin: 50px auto  0 auto;
        width: 450px;
        #confirm{
            width: 100%;
            font-size: 20px;
        }
    }
</style>