<template>
    <div id="flex">
        <div class="student">
            <h4 class="name">{{info.full_name}}</h4>
            <span class="num">{{info.name}}</span>
        </div>
        <div class="orgs"><span>{{orgs[0]}}</span></div>
        <div class="stats">
            <h4 class="uncommitted">{{stats.uncommitted}}</h4>
            <h4 class="revising">{{stats.revising}}</h4>
            <h4 class="improvable">{{stats.improvable}}</h4>
            <h4 class="finished">{{stats.finished}}</h4>
        </div>
        <div class="buttonCtrl">
            <a href="http://vipgit.chanke.xyz/user/login" target="_blank">
              <a-button  icon="folder"></a-button>
            </a>
        <a-button class="logout"  icon="logout" @click="logout"></a-button>
        </div>
    </div>
</template>
<script>
    import {mapState, mapActions} from 'vuex'
    export default{
        computed: {
            ...mapState("studentsinfo", ["stats", "orgs"]),
            ...mapState("user", ["info"]),
        },
        methods:{
            logout(){
                this.$store.dispatch("user/Aclogout");
                this.$message.info("")
            }
        }
}
</script>
