#flex{
    display: flex;
    justify-content: space-between;
    height: 64px;
    width: 1000px;
    h4{
        display: block;
        color: white;
    }
    .student{
        display: flex;
        flex-direction: column;
        justify-content: center;
         span{
             height: 20px;
         }
        .num{
            margin-top: 5px;
        }
    }
    .orgs{
        line-height: 64px;
    }
    .stats{
        display: flex;
        width: 500px;
        justify-content: space-between;
        line-height: 64px;
    }
    .buttonCtrl{
        line-height: 64px;
         .logout{
             background-color: white;
             margin-left: 15px;
         }
    }
}
</style>