const state ={
    assignments:[],//
    resources:[],
    orgs:[],//
};
const getters ={

};
const mutations ={
    getInit(state,data){
        state.assignments=data.assignments;
        state.resources=data.resources;
        state.orgs=data.orgs;
    }
};
const actions ={
    Dreload({commit},data){
        commit("getInit",data)
    }
};
export default {
    namespaced:true,
    state,getters,mutations,actions
}