<template>
<div class="container">
    <div class="userIcon">
        <a-avatar shape="square" size="large" class="icon">{{info.full_name}}</a-avatar>
    </div>
    <div class="uploadLesson">
        <a-button icon="file-add" type="primary" @click="uploadLesson"></a-button>
    </div>
    <div class="buttonCtrl">
        <a href="http://vipgit.chanke.xyz/user/login" target="_blank">
            <a-button  icon="folder"></a-button>
        </a>
        <a-button class="logout"  icon="logout" @click="logout"></a-button>
    </div>
    <a-modal :visible="visible" @cancel="visible=false" :footer="null">
          <a-form>
              <a-form-item  label="">
                  <a-select v-model="everyLesson.org_id">
                      <a-select-option v-for="(full_name,id) in LessonModel" :value="id" :key="id">
                          {{full_name}}
                      </a-select-option>
                  </a-select>
              </a-form-item>
              <a-form-item  label="">
                  <a-textarea  v-model="everyLesson.name" autosize></a-textarea>
              </a-form-item>
              <a-form-item  label="/">
                  <a-range-picker @change="onChange" />
              </a-form-item>
              <a-form-item>
                  <a-button @click="saveOn(everyLesson.org_id)" type="primary"></a-button>
              </a-form-item>
          </a-form>
    </a-modal>
</div>
</template>
<script>
import {api} from "../utils/api"
import {mapState, mapActions} from 'vuex'
import ABreadcrumbItem from "ant-design-vue/es/breadcrumb/BreadcrumbItem";
import AButton from "ant-design-vue/es/button/button";
import AModal from "ant-design-vue/es/modal/Modal";
const LessonModel= {
    "4":"",
    "41":"",
    "69":"",
    "70":"",
    "88":"",
    "89":"",
};
const studentCount=[{
    id:"4",
    AllCount:"16"
},{
    id:"41",
    AllCount:"18"
},
    {
        id:"69",
        AllCount:"34"
    },
    {
        id:"70",
        AllCount:"21"
    },
    {
        id:"88",
        AllCount:"27"
    },
    {
        id:"89",
        AllCount:"18"
    }]
export default{
    data(){
        return{
            visible:false,
            LessonModel,
            studentCount,
            everyLesson:{
                org_id:"41",
                name:" ",
                start_time:"0000-00-00",
                end_time:"0000-00-00"
            },
        }
    },
    components: {
        AModal,
        AButton,
        ABreadcrumbItem,
    },
    computed: {
        ...mapState("user", ["info"]),
        ...mapState("teacherInfo",["orgs","assignments"])
    },
    methods:{
        logout(){
            this.$store.dispatch("user/Aclogout");
            this.$message.info("")
        },
        uploadLesson(){
            this.visible=true;
            console.log(this.orgs,this.assignments);
        },
        onChange(date, dateString){
            this.everyLesson.start_time=dateString[0];
            this.everyLesson.end_time=dateString[1];
        },
        saveOn(id){
              const student_countData=this.studentCount.find(item=>{
                  if(item.id===id){
                      return item.AllCount
                  }
              });
            api.post("/teacher/createAssignment",this.everyLesson).then(data=>{
                data.student_count=student_countData.AllCount;
                this.assignments.unshift(data)
            }).finally(()=>{
                this.visible=false;
            })
        }
    }
}
</script>
<style lang="scss" scoped>
    .container {
        display: flex;
        justify-content: space-between;
        height: 64px;
        width: 500px;
        .userIcon {
            .icon {
                background-color: cornflowerblue;
            }
        }
        .buttonCtrl {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .logout {
                margin-left: 15px;
            }

        }
    }

</style>