const state ={
    assignments:[],
    stats:{

    },
    orgs:[],
};
const getters ={

};
const mutations ={
    getInit(state,data){
        state.assignments=data.assignments;
        state.stats=data.stats;
        state.orgs=data.orgs;
    }
};
const actions ={
     Dreload({commit},data){
         commit("getInit",data)
     }
};
export default {
    namespaced:true,
    state,getters,mutations,actions
}