<template>
    <div class="teacher">
        <a-table :dataSource="assignments"
                 :columns="columns"
                 :pagination="false"
                 :rowKey="assignments=>assignments.assignment_id"
                 class="animated"
        >
           <template slot="workState" slot-scope="text,record,index">
               <div  class="WorkState">
                   <div class="flexRow">
                       <span>{{record.student_count}}/</span>
                       <span :class="record.work_count/record.student_count<0.5?'errorText':'success'"
                       >{{record.work_count}}</span>
                       <span>&nbsp&nbsp</span>
                       <span v-if="record.work_count>0">- &nbsp{{record.work_update_time}}</span>
                       &nbsp&nbsp<a-button @click="drawLine(record)"></a-button>
                   </div>
               </div>
           </template>
            <template slot="operation" slot-scope="text,record,index">
                 <div v-if="record.work_count==0">
                     <a-popconfirm title="?" @confirm="confirm(record.assignment_id)"  okText="" cancelText="">
                       <a-button type="danger" icon="delete" @click="Callback"></a-button>
                     </a-popconfirm>
                 </div>
                <div v-else>
                    <a-button type="primary"  icon="down-square" v-if="!isCheck" @click="isCheck=record;record.status=WorkStatus.status"></a-button>
                    <a-button type="primary"  icon="up-square" v-else @click="isCheck=false"></a-button>
                    <a :href="`${downloadUrl}All?id=${record.assignment_id}`">
                        <a-button icon="download"></a-button>
                    </a>
                </div>
            </template>
        </a-table>
        <transition enterActiveClass="zoomIn" leaveActiveClass="zoomOut" mode="out-in">
        <a-table
                v-if="isCheck"
                :dataSource="isCheck.works"
                :columns="WorkColumns"
                :pagination="false"
                class="animated"
                :rowKey="work=>work.id"
        >
          <template slot-scope="text, record, index" slot="status">
                <a-alert :showIcon="false"  type="info" message="" banner v-if="record.status==0"/>
                <a-alert :showIcon="false"  message="" banner v-if="record.status==1"/>
                <a-alert :showIcon="false" type="success" message="" banner v-if="record.status==2"/>
          </template>
            <template slot="operation" slot-scope="text, record, index">
                <a :href="`${downloadUrl}?id=${record.id}&type=student`">
                    <a-button icon="download"></a-button>
                </a>
                <a-button icon="edit" type="primary" @click="isCheckWork=record;correctWorkFrom.review=record.teacher_review"></a-button>
            </template>
        </a-table>
        </transition>
        <a-modal :visible="Boolean(isCheckWork)" @cancel="isCheckWork=false" :footer="null">
            <a-form>
                <a-form-item  label="">
                    <a-select v-model="correctWorkFrom.status">
                        <a-select-option v-for="(value,key) in WorkStatus" :value="key" :key="key">
                            {{value}}
                        </a-select-option>
                    </a-select>
                </a-form-item>
                <a-form-item  label="">
                    <a-textarea  v-model="correctWorkFrom.review" autosize></a-textarea>
                </a-form-item>
                <a-form-item label="">
                    <a-upload name="file"  :action="upLoadUrl" :headers="{Token:info.token}"
                              :data="{id:isCheckWork.id}" @change="changehandle"
                              :showUploadList="false"
                    >
                        <a-button>
                            <a-icon type="upload" />
                        </a-button>
                    </a-upload>
                </a-form-item>
                <a-form-item>
                    <a-button
                            type="primary" @click="saveMS">
                        
                    </a-button>
                </a-form-item>
            </a-form>
        </a-modal>
        <div class="myModal" v-show="Boolean(workStateVisible)">
            <div id="myChart">
            </div>
            <p class="student_count">:&nbsp&nbsp{{workStateVisible.student_count}}</p>
            <a-button class="Okbutton"  type="primary" @click="workStateVisible=false"></a-button>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import {api} from "../utils/api"
    import AButton from "ant-design-vue/es/button/button";
    import AFormItem from "ant-design-vue/es/form/FormItem";
    import ATextarea from "ant-design-vue/es/input/TextArea";
    import AModal from "ant-design-vue/es/modal/Modal";
    const baseUrl="http://sandbox_api.estudy.chanke.xyz";
    const WorkStatus={
        "0":"",
        "1":"",
        "2":""
    };
    export default{
        components: {
            AModal,
            ATextarea,
            AFormItem,
            AButton,
        },
        computed:{
           ...mapState("teacherInfo",["assignments"]),
            ...mapState("user",["info"]),
            columns(){
               return [
                    {
                        width:"150px",
                        key: "lesson",
                        align: "center",
                        title: "",
                        dataIndex: "org_name"
                    },
                    {
                        width:"150px",
                        key: "lessonName",
                        align: "center",
                        title: "",
                        dataIndex: "name"
                    },
                    {
                        width:"250px",
                        align: "center",
                        key:"time",
                        title: "-",
                        customRender(text, record, index){
                            return `${record.start_time}~${record.end_time}`
                        }
                    }, {
                        width:"250px",
                        align: "center",
                        key:"workState",
                        title: "",
                        scopedSlots: { customRender: 'workState' },
                    },
                    {
                        width:"250px",
                        align: "center",
                        key:"operation",
                        title: "",
                        scopedSlots: { customRender: 'operation' },
                        filteredValue:this.isCheck?[this.isCheck.assignment_id] :null,
                        onFilter(value, record){
                             return value===record.assignment_id;
                        }
                    }
                ]
            }
       },
        data(){
           return{
               WorkStatus,
               workStateVisible:false,
               correctWorkFrom:{
                   status:"1",
                   review:'',
               },
               isCheckWork:false,
               isCheck:false,
               downloadUrl:baseUrl+"/teacher/download",
               upLoadUrl:baseUrl+"/teacher/upload",
               WorkColumns:[{
                       width:"150px",
                       key: "studentName",
                       align: "center",
                       title: "",
                       dataIndex: "user.full_name"
               },
                   {
                       width:"150px",
                       key: "studentNum",
                       align: "center",
                       title: "",
                       dataIndex: "user.name"
                   },
                   {
                       width:"150px",
                       key: "studentWorkName",
                       align: "center",
                       title: "",
                       dataIndex: "student_upload_name"
                   },{
                       width:"150px",
                       key: "commitTime",
                       align: "center",
                       title: "",
                       dataIndex: "commit_time"
                   },
                   {
                       width:"200px",
                       key: "status",
                       align: "center",
                       title: "",
                       scopedSlots: { customRender: 'status' },
                   },
                   {
                       align:"center",
                       width:"250px",
                       key:"operation",
                       title: "",
                       scopedSlots: { customRender: 'operation' },
                   },
               ],
           }
        },
        methods:{
            drawLine(record){
                this.workStateVisible=record;
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        orient: 'horizontal',
                        x: 'center',
                        data:['','']
                    },
                    series: [
                        {
                            name:'',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '24',
                                        fontWeight: 'bold'
                                    }
                                }
                            },

                            data:[
                                {value:this.workStateVisible.work_count, name:'',itemStyle: {
                                    color: 'green'
                                }},
                                {value:this.workStateVisible.student_count-this.workStateVisible.work_count, name:'',itemStyle: {
                                    color: 'red'
                                }},
                            ]
                        }
                    ]
                });
            },
            confirm(id){
               api.post("/teacher/deleteAssignment",{id}).then(data=>{
                   const index=this.assignments.findIndex(assignment=>assignment.assignment_id===id);
                   this.assignments.splice(index,1);
                   this.$message.info("")
               });
            },
            Callback(){
                //dosomthing
            },
            saveMS(){
               const form={
                   id:this.isCheckWork.id,
                   status:this.correctWorkFrom.status,
                   review:this.correctWorkFrom.review,
               };
               api.post("/teacher/review",form).then(data=>{
                   Object.entries(data).forEach(item =>{this.$set(this.isCheckWork,item[0],item[1])})
                   console.log(data)
               }).finally(()=>{
                  this.isCheckWork=false
               })
            },//
            changehandle(info){

                if(info.file.status==="done"){
                    this.$message.success("")
                }else if(info.file.status==="error"){
                    this.$message.error(info.file.response.errorMessage)
                }
            }
        }

    }
</script>
<style lang="scss" scoped>
.teacher{
    position: relative;
    width: 100%;
    height: 100%;
    .myModal{
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
        user-select: none;

        #myChart{
            position: absolute;
            top:50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 600px;
            height: 300px;
            background-color: whitesmoke;
            border-radius: 10px;
        }
        .student_count{
            position: absolute;
            top:50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }
        .Okbutton{
            position: absolute;
            top: 550px;
            left: 50%;
            transform: translate(-50%);
        }
    }
    .WorkState{
        .flexRow{
            display: flex;
            justify-content: center;
            align-items: center;
            span{
                text-align: center;
                height: 100%;
            }
            .errorText{
                color: red;
            }
            .success{
                color: greenyellow;
            }
        }

    }
}


</style>