<template>
    <div>
        <a-table :rowKey="assignments=>assignments.assignment_id"
                 :columns="columns" :dataSource="assignments" :pagination="false">
          <template slot="operation" slot-scope="text, record, index">
              <a-upload name="file"  :action="actionUrl" :headers="{Token:info.token}"
                  :data="{id:record.assignment_id}" @change="changehandle"
                        :showUploadList="false"
              >
                  <a-button>
                      <a-icon type="upload" />
                  </a-button>
              </a-upload>
              <a :href="`${downloadUrl}?id=${record.work.id}&type=student`" target="_blank" v-if="record.work">
                  <a-button icon="download"></a-button>
              </a>
          </template>
            <template slot-scope="text, record, index" slot="uploadStatus">
                <a-alert :showIcon="false" type="error" message="" banner v-if="!record.work"/>
                <a-alert :showIcon="false"  type="info" message="" banner v-else-if="record.work.status==0"/>
                <a-alert :showIcon="false"  message="" banner v-else-if="record.work.status==1"/>
                <a-alert :showIcon="false" type="success" message="" banner v-else/>
            </template>
            <template slot="uploadMs" slot-scope="text, record, index">
                <div v-if="record.work">
                    <div v-if="record.work.status==0">
                       <p v-if="record.work.teacher_download_time.slice(0,10)=='0000-00-00'">{{record.work.commit_time}}&nbsp
                        </p>
                         <p v-else>{{record.work.teacher_download_time}}&nbsp</p>
                     </div>
                    <div v-else-if="record.work.status==1">
                        <p>{{record.work.review_time}}&nbsp</p>
                         <a-button type="primary" @click="visible=record.work"></a-button>
                    </div>
                    <div v-else>
                        <p>{{record.work.review_time}}&nbsp</p>
                        <a-button type="primary" @click="visible=record.work"></a-button>
                    </div>
                </div>
            </template>
        </a-table>
        <a-modal :visible="Boolean(visible)" @cancel="visible=false"  @ok="visible=false"
                  :keyboard="true" :closable="false" >
            <template slot="footer">
                <a-button key="submit" type="primary"  @click="visible=false">
                    
                </a-button>
            </template>
            <h4 v-if="visible.status==1"></h4>
            <h4 v-else></h4>
            <p v-if="visible&&visible.teacher_review">{{visible.teacher_review}}</p>
            <div v-if="visible.teacher_upload_name">
                <a :href="`${downloadUrl}?id=${visible.id}&type=teacher`" target="_blank" >
                    <a-button icon="file"></a-button>
                </a>
            </div>
        </a-modal>
    </div>
</template>
<script>
    const baseUrl="http://sandbox_api.estudy.chanke.xyz";
    import {mapState} from 'vuex'
    import AButton from "ant-design-vue/es/button/button";
    import AModal from "ant-design-vue/es/modal/Modal";
    export default{
        components: {
            AModal,
            AButton},
        computed:{
            ...mapState("studentsinfo",["assignments"]),
            ...mapState("user", ["info"]),
        },
        data(){
            return {
               visible:false,
               actionUrl:baseUrl+"/student/upload",
               downloadUrl:baseUrl+"/student/download",
                columns: [
                    {key:"lesson",
                        title: "",
                        dataIndex: "org_name"
                    }, {
                        key:"lessonName",
                        title: "",
                        dataIndex: "name"
                    }, {
                        width:"250px",
                        key:"time",
                        title: "-",
                        customRender(text, record, index){
                            return `${record.start_time}~${record.end_time}`
                        }
                    },
                    {
                        align:"center",
                        width:"250px",
                        key:"operation",
                        title: "",
                        scopedSlots: { customRender: 'operation' },
                    },
                    {
                        align:"center",
                        width:"200px",
                        key:"uploadStatus",
                        title: "",
                        scopedSlots: { customRender: 'uploadStatus' },
                    },
                    {
                        align:"center",
                        key:"uploadMs",
                        title: "",
                        scopedSlots: { customRender: 'uploadMs' },
                    },
                ]
            }
        },
        methods:{
            changehandle(info){
                if(info.file.status==="done"&&info.file.response.data){
                         this.$message.success("")
                }else if(info.file.status==="error"){
                this.$message.error(info.file.response.errorMessage)
                }
            },
        }
    }
</script>
<style></style>